import { memo } from 'react';
import styled from 'styled-components';

import { Box } from '@/Global/components/commonComponents';
import HeaderWithLine from '@/Global/components/HeaderWithLine/HeaderWithLine';
import { FONT_SIZE, FONT_WEIGHT, LINE_HEIGHT, SCREEN_WIDTH } from '@/Global/globalStyles';
import aboutUsPhoto from '@/resources/Images/aboutUsPhoto.jpg';

interface HomeInformationProps {
    className?: string;
}

const HomeInformation = ({ className }: HomeInformationProps) => {
    return (
        <div id="about" className={className}>
            <TextSectionWrapper>
                <HeaderWithLine text="Domeko okna i drzwi" />
                <StyledText>
                    Jestemy dynamiczn firm, ktra oferuje kompleksowe usugi w dziedzinie doradztwa, transportu oraz
                    sprzeday okien i drzwi. Nasza firma jest z dum partnerem renomowanej marki Wood Natural, co
                    pozwala nam dostarcza najwyszej jakoci produkty i usugi dla naszych klientw. Jeli potrzebujesz
                    profesjonalnego wsparcia w wyborze okien i drzwi, skontaktuj si z nami. Jestemy gotowi
                    odpowiedzie na Twoje pytania i zapewni rozwizania, ktre speni Twoje oczekiwania. Wood Glass
                    Team to pewno jakoci i satysfakcji klienta.
                </StyledText>
            </TextSectionWrapper>
            <Wrapper>
                <StyledImg src={aboutUsPhoto} alt="logo" />
            </Wrapper>
        </div>
    );
};

const Memoized = memo(HomeInformation);
const Styled = styled(Memoized)`
    margin: auto;
    display: flex;
    justify-content: space-around;
    max-width: 1800px;
    padding-top: 8rem;
    @media (max-width: ${SCREEN_WIDTH.xlargeMax}) {
        padding-top: 3rem;
        flex-direction: column;
        justify-content: center;
    }
`;

const Wrapper = styled.div`
    padding: 0 4rem 0 0;
    @media (max-width: ${SCREEN_WIDTH.xlargeMax}) {
        padding: 0;
        display: flex;
        justify-content: center;
    }
`;

const StyledImg = styled.img`
    width: 720px;

    @media (max-width: ${SCREEN_WIDTH.large}) {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
`;

const TextSectionWrapper = styled(Box)`
    flex-direction: column;
    padding: 0 6rem;

    @media (max-width: ${SCREEN_WIDTH.xlargeMax}) {
        padding: 0 3rem 2rem;
    }
`;

const StyledText = styled.p`
    font-weight: ${FONT_WEIGHT.normal};
    font-size: ${FONT_SIZE.normal};
    line-height: ${LINE_HEIGHT.xlarge};

    @media (max-width: ${SCREEN_WIDTH.xlargeMin}) {
        font-size: ${FONT_SIZE.normal};
        line-height: ${LINE_HEIGHT.medium};
    }
`;
export default Styled;
